import { Fragment, useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { blogs } from "common/blogs";
import Seo from "common/seo";

const DevelopmentBlog = () => {
  const location = useLocation();
  const [selectedSection, setSelectedSection] = useState("");

  useEffect(() => {
    const handleScrollEffect = () => {
      const sections = document.querySelectorAll("h5");
      let currentSection = "";
      sections.forEach((section) => {
        const rect = section.getBoundingClientRect();
        if (rect.top >= 0 && rect.top <= window.innerHeight / 5) {
          currentSection = section.id;
        }
      });
      if (currentSection && currentSection !== selectedSection) {
        setSelectedSection(currentSection);
      }
    };
    window.addEventListener("scroll", handleScrollEffect);
    return () => {
      window.removeEventListener("scroll", handleScrollEffect);
    };
  }, [selectedSection]);

  const blog = blogs?.find(
    (e) => e?.slug === location.pathname?.split("/")?.[2]
  );
  console.log("blog::", blog);

  const handleOnChange = (e) => {
    const sectionId = e.target.value;
    if (sectionId) {
      const section = document.getElementById(sectionId);
      if (section) {
        const offset = 100; // Adjust this value to your desired offset
        const sectionPosition =
          section.getBoundingClientRect().top + window.scrollY - offset;
        window.scrollTo({
          top: sectionPosition,
          behavior: "smooth",
        });
      }
    }
  };

  return (
    <Fragment>
      <Seo
        title={blog?.stack}
        metaTitle={blog?.title}
        description={blog?.description}
      />
      <div className="page_wrapper">
        <div className="container">
          <div className="row">
            <div className="col-12 col-xs-12 col-md-8 col-xl-10 m-auto">
              <div className="blog_details_wrapper">
                <div className="blog_details_header">
                  <span className="fw-300 text-1">{blog?.stack}</span>
                  <h1 className="text-8 fw-600 wow animate__ animate__fadeInUp  animated">
                    {blog?.title}
                  </h1>
                </div>
                <div className="blog_img_sec">
                  <img src={blog?.image} alt="blog-details-img" />
                </div>
                <div className="blog_short_desc_sec">
                  <p className="text-3">{blog?.description}</p>
                </div>
                <div className="blog_select_box_sec">
                  <select
                    className="form_select"
                    onChange={handleOnChange}
                    value={selectedSection}
                  >
                    <option>Table Contents</option>
                    <option value="research_your_destination">
                      Research Your Destination
                    </option>
                    <option value="plan_your_itinerary">
                      Plan Your Itinerary
                    </option>
                    <option value="pack_lightly_and_smartly">
                      Pack Lightly and Smartly
                    </option>
                    <option value="immerse_yourself_in_the_local_culture">
                      Immerse Yourself in the Local Culture
                    </option>
                    <option value="conclusion">Conclusion</option>
                  </select>
                </div>
                <div className="blog_details_desc_sec">
                  <div className="blog_content_sec">
                    <h5 id="research_your_destination">
                      Research Your Destination
                    </h5>
                    <p className="text-3">
                      Before embarking on your journey, take the time to
                      research your destination. This includes understanding the
                      local culture, customs, and laws, as well as identifying
                      top attractions, restaurants, and accommodations. Doing so
                      will help you navigate your destination with confidence
                      and avoid any cultural faux pas.
                    </p>
                  </div>
                  <div className="blog_content_sec">
                    <h5 id="plan_your_itinerary">Plan Your Itinerary</h5>
                    <p className="text-3">
                      While it's essential to leave room for spontaneity and
                      unexpected adventures, having a rough itinerary can help
                      you make the most of your time and budget. Identify the
                      must-see sights and experiences and prioritize them
                      according to your interests and preferences. This will
                      help you avoid overscheduling and ensure that you have
                      time to relax and enjoy your journey.
                    </p>
                  </div>
                  <div className="blog_content_sec">
                    <h5 id="pack_lightly_and_smartly">
                      Pack Lightly and Smartly
                    </h5>
                    <p className="text-3">
                      Packing can be a daunting task, but with some careful
                      planning and smart choices, you can pack light and
                      efficiently. Start by making a packing list and sticking
                      to it, focusing on versatile and comfortable clothing that
                      can be mixed and matched. Invest in quality luggage and
                      packing organizers to maximize space and minimize
                      wrinkles.
                    </p>
                  </div>
                  <div className="blog_content_sec">
                    <h5 id="immerse_yourself_in_the_local_culture">
                      Immerse Yourself in the Local Culture
                    </h5>
                    <p className="text-3">
                      One of the most rewarding aspects of traveling is
                      immersing yourself in the local culture and customs. This
                      includes trying local cuisine, attending cultural events
                      and festivals, and interacting with locals. Learning a few
                      phrases in the local language can also go a long way in
                      making connections and showing respect.
                    </p>
                  </div>
                  <div className="blog_content_sec">
                    <h5 id="conclusion">Conclusion:</h5>
                    <p className="text-3">
                      Traveling is an art form that requires a blend of
                      planning, preparation, and spontaneity. By following these
                      tips and tricks, you can make the most of your journey and
                      create memories that last a lifetime. So pack your bags,
                      embrace the adventure, and enjoy the ride.
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default DevelopmentBlog;
