import { Link } from "react-router-dom";
import { blogs } from "common/blogs";
import { useCallback, useEffect, useState } from "react";
import Loading from "common/components/spinner";

const BlogsScreen = () => {
  const readMoreIcon = () => {
    return (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="20"
        height="20"
        viewBox="0 0 24 24"
        fill="none"
        stroke="currentColor"
        stroke-width="2"
        stroke-linecap="round"
        stroke-linejoin="round"
        data-lucide="arrow-up-right"
        class="lucide lucide-arrow-up-right"
      >
        <path d="M7 7h10v10"></path>
        <path d="M7 17 17 7"></path>
      </svg>
    );
  };

  const [data, setData] = useState(blogs.slice(0, 10)); // initial 10 objects
  const [page, setPage] = useState(1); // current page number
  const [isFetchingMoreData, setIsFetchingMoreData] = useState(false); // loading indicator
  const [bottomBoundaryRef, setBottomBoundaryRef] = useState(null);

  useEffect(() => {
    // handle called ref event trigger the after 10 data load method
    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          loadMore();
        }
      },
      {
        rootMargin: "0px",
        threshold: 1.0,
      }
    );

    if (bottomBoundaryRef) {
      observer.observe(bottomBoundaryRef);
    }

    return () => {
      if (bottomBoundaryRef) {
        observer.unobserve(bottomBoundaryRef);
      }
    };
  }, [isFetchingMoreData, bottomBoundaryRef]);

  const loadMore = useCallback(() => {
    if (page * 10 >= blogs.length) {
      return;
    }
    setIsFetchingMoreData(true);
    setTimeout(() => {
      const loadMoreData = () => {
        const nextPage = page + 1;
        const newData = blogs?.slice((nextPage - 1) * 10, nextPage * 10);
        console.log("newData::", newData);
        setData([...data, ...newData]);
        setPage(nextPage);
        if (nextPage * 10 >= blogs?.length) {
          setIsFetchingMoreData(false);
        } else {
          setIsFetchingMoreData(false);
        }
      };
      loadMoreData();
    }, 1000); // add a delay of 500ms
  }, [page, data]);

  useEffect(() => {
    console.log("data::", data);
  }, [data]);

  return (
    <div className="page_wrapper">
      <div className="blog_header">
        <h1 className="text-10 fw-600 wow animate__ animate__fadeInUp  animated">
          Blog
        </h1>
        <p className="text-muted">
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry.
        </p>
      </div>
      <div className="blog_list_wrapper">
        <div className="container blog-container">
          {data?.map((ele, ind) => {
            const { slug } = ele;
            return (
              <div className="row" key={ind}>
                <div className="col-xs-12 col-sm-12 col-md-6">
                  <div
                    className="blog_image_wrapper wow fadeIn"
                    data-wow-delay="1s"
                    style={{ visibility: "visible", animationDelay: "1s" }}
                  >
                    <img src={ele?.image} alt="blog-1" />
                  </div>
                </div>
                <div
                  data-wow-duration="1s"
                  className="col-xs-12 col-sm-12 col-md-6 wow slideInRight  animated"
                >
                  <div className="blog_details_head">
                    <p className="text-2 text-decoration-underline">
                      {ele?.stack}
                    </p>
                    <span className="text-2 mb-0 fw-300 text-muted">
                      {ele?.date}
                    </span>
                  </div>
                  <div className="blog_details_body">
                    <h2 className="text-5">{ele?.title}</h2>
                    <p className="text-3 text-muted">{ele?.description}</p>
                  </div>
                  <div className="blog_details_footer">
                    <Link
                      className="text-decoration-none fw-400 text-3"
                      to={`/blogs/${slug}`}
                    >
                      Read More {readMoreIcon()}
                    </Link>
                  </div>
                </div>
              </div>
            );
          })}
          <div ref={setBottomBoundaryRef} />
          {isFetchingMoreData && <Loading />}
        </div>
      </div>
    </div>
  );
};

export default BlogsScreen;
